.card {
    max-width: 296px;
    cursor: pointer;
    margin: auto;
}

.card:hover {
    box-shadow: 0px 0px 6px 3px #676767;
    -moz-box-shadow: 0px 0px 6px 3px #676767;
    -webkit-box-shadow: 0px 0px 6px 3px #676767;
}

.media {
    height: 150px;
    margin: 10px auto 0 auto;
}



